h1 {
  text-align: center;

  .texte-bleu {
    color: $color-blue;
  }
}

.catalogue {
  display: flex;
  padding: 0 0 100px 0;

  .filtre-bloc {
    min-width: 200px;
    position: sticky;
    margin: 10px 20px 0 30px;

    button {
      background: whitesmoke;
      border: 0;
      color: grey;
      font-size: 0.75rem;
      cursor: pointer;

      &:hover {
        color: lightgrey;
        transition: color 0.3s ease;
      }
    }

    h2 {
      font-size: 1rem;
      display: flex;
      justify-content: space-between;
    }

    ul {
      margin: 0;
      padding: 0 0 20px 10px;

      li {
        list-style: none;
        padding: 2px;

        label {
          cursor: pointer;

          &:hover {
            color: $color-light-blue;
            transition: color 0.3s ease;
          }
        }


        input {
          margin: 3px 10px 3px 4px;
          transform: scale(1.3);
        }
      }
    }

  }

  .produits-bloc {
    flex-grow: 1;

    .tri {
      padding: 0 20px;

      select {
        padding: 0 5px;
        border: 0;
        background: whitesmoke;
        font-size: 0.95rem;
        cursor: pointer;

        &:hover {
          color: $color-blue;
        }

        option {
          color: $color-black;
        }
      }

      button {
        margin: 0 0 0 10px;
        padding: 3px 10px 2px;
        background: $color-dark-blue;
        border: 1px solid $color-dark-blue;
        border-radius: 4px;
        color: whitesmoke;
        font-size: 0.9rem;
        cursor: pointer;

        &:hover {
          color: $color-light-blue;
          transition: color 0.3s ease;
        }
      }
    }

    .produits ul {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      padding: 0;

      li {
        list-style: none;
        height: 250px;
        width: 300px;
        border: 1px solid rgba(128, 128, 128, 0.3);
        border-radius: 10px;
        margin: 10px;
        padding: 5px;
        position: relative;

        a {
          text-decoration: none;
          color: $color-black;

          &:hover {
            color: $color-blue;
          }

          img {
            margin: 0 auto;
            display: block;
            max-width: 250px;
            max-height: 150px;
            padding-top: 10px;
          }

          h2 {
            font-size: 1rem;
            text-align: center;
            margin: 10px 20px;
          }

          p {
            text-align: center;
            padding: 0 0 17px 20px;
            margin: 0;
            position: absolute;
            bottom: 0;
          }
        }


      }
    }
  }
}