.head {


  .info {
    display: flex;
    justify-content: right;

    p {
      padding-right: 3%;
      padding-left: 3%;
      font-size: 0.8rem;
      cursor: pointer;
      white-space: nowrap;

      &:hover {
        color: #BFB4B4;
        transition: color 0.3s ease;
        cursor: pointer;
      }


    }


  }

  .search-connection {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: $color-dark-blue;
    color: whitesmoke;
    padding-top: 0;
    height: 60px;

    img {
      margin-left: 2%;
      width: 150px;
      height: 60px;
      cursor: pointer;
    }

    .search {
      background-color: whitesmoke;
      border-radius: 10px;
      display: flex;
      align-items: center;
      width: 600px;
      padding: 0;
      margin: 0;

      input {
        border: none;
        border-radius: 10px;
        font-size: 1rem;
        padding: 10px;
        width: 75%;
        outline: none;
        background-color: whitesmoke;
        margin: 0;

      }

      label {
        color: $color-black;
        border: 2px solid $color-light-blue;
        padding: 8px;
        flex: 1;
        background-color: $color-light-blue;
        color: whitesmoke;
        border-radius: 10px;
        height: 100%;
        text-align: center;
        margin: 0;

        &:hover {
          color: #5d5d5d;
          transition: color 0.3s ease;
          cursor: pointer;
        }
      }
    }

    .connection-panier {
      display: flex;
      justify-content: space-between;
      margin-right: 2%;

      svg {
        padding: 0 4px;
      }

      .connection {
        display: flex;
        justify-content: space-between;
        align-items: center;
        cursor: pointer;
        white-space: nowrap;

        &:hover {
          color: #BFB4B4;
          transition: color 0.3s ease;
        }
      }

      .pannier {
        display: flex;
        align-items: center;
        margin-left: 40px;
        cursor: pointer;

        &:hover {
          color: #BFB4B4;
          transition: color 0.3s ease;
        }

      }


    }
  }
}
