.nav-container {
  font-family: Arial;
  position: relative;
  margin-bottom: 30px;
  background-color: #e6e2e2;
  width: 100%;



  margin-top: 20px;

  ul {
    display: flex;
    justify-content: center;
    align-content: center;
    list-style: none;
    margin: 20px;

    a {
      text-decoration: none;
      padding: 10px;
      margin-right: 80px;
      justify-content: center;
      white-space: nowrap;


      li {

        transition: 0.2s;
        cursor: pointer;
        padding: 5px;
        font-weight: bold;
        cursor: pointer;
        color: $color-black;

        &:hover {
          color: $color-light-blue;
          transition: color 0, 3s ease;
        }
      }
    }

    .nav-active {
      position: relative;
      background-color: $color-light-blue;
      border-radius: 10px;

      li {
        &:hover {
          color: #3477a0;
        }
      }


    }
  }
}