.home-body {
  padding: 0 0 100px 0;

  .accueil {
    color: #2757A1;
    font-size: 3rem;
    text-align: center;
  }

  .home-block {
    display: flex;
    flex-direction: column;
    gap: 100px;

    .nouveaute-carrousel {
      display: flex;
      flex: 1;
      background-color: #DCF3F4;
      height: 450px;
      weight: 300px;

      .titre {
        font-size: 1.9rem;
      }

      .texte {
        font-size: 1.4rem;
        padding: 10px;
        color: #666666;
      }

      .texte-bleu {
        color: #2757A1;
      }

      .new-products {
        flex: 1;
        padding: 10px 20px;
        text-align: center;
      }

      .product-carousel {
        flex: 1;

        .display-image {
          height: 500px;
          weight: 300px;
          object-fit: cover;
        }
      }
    }


    .top-ventes {
      background-color: #DCF3F4;
      padding: 10px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;

      .titre-vente {
        font-size: 1.9rem;
        text-align: center;

        .titre-couleur {
          color: #2757A1;
        }
      }

      .produits ul {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        padding: 0;

        li {
          list-style: none;
          height: 250px;
          width: 300px;
          border: 1px solid rgba(128, 128, 128, 0.3);
          border-radius: 10px;
          margin: 10px;
          padding: 5px;
          position: relative;

          a {
            text-decoration: none;
            color: $color-black;

            &:hover {
              color: $color-blue;
            }

            img {
              margin: 0 auto;
              display: block;
              max-width: 250px;
              max-height: 150px;
              padding-top: 10px;
            }

            h2 {
              font-size: 1rem;
              text-align: center;
              margin: 10px 20px;
            }

            p {
              text-align: center;
              padding: 0 0 17px 20px;
              margin: 0;
              position: absolute;
              bottom: 0;
            }
          }
        }
      }
    }
  }


}