.infos-compte-container {
  margin: 30px 30px 100px;
  display: flex;
  flex-direction: column;
  align-items: center;

  .infos-compte {
    border: 1px solid $color-dark-blue;
    border-radius: 20px;
    padding: 0 30px;
    max-width: 600px;
    width: 100%;

    .titre {
      margin: 30px 0 0;
      font-family: Poppins, serif;
      color: $color-dark-blue;
      font-size: 1.6rem;
      font-weight: bold;
      text-align: center;
    }

    .infos-titre {
      font-family: Poppins, serif;
      margin: 50px 0 10px;
      font-size: 1.2rem;
      color: grey;
    }

    .infos-valeur {
      font-family: Poppins, serif;
      margin: 10px 0 50px;
      font-size: 1.3rem;
    }
  }


}

