footer {
  width: 100%;
  margin-top: auto;
  background-color: rgb(254, 254, 254);
  background-image: url("../../assets/img/footer/topographie.png");

  ul {
    list-style: none;
    padding: 0;
    margin: 0;

    li a {
      display: block;
      font-size: 14px;
      text-transform: capitalize;
      color: #918c8c;
      text-decoration: none;
      transition: 0.2s;
      padding: 2px 0;

      &:hover {
        color: $color-dark-blue;
        padding-left: 1px;
      }
    }
  }

  .footer-info {
    display: flex;
    justify-content: space-around;
    border: 1px solid $color-dark-blue;


    .footer-col {
      padding: 30px 20px;

      h4 {
        margin: 10px 0;
      }

      .links {
        display: flex;
        align-content: space-around;


      }

      .transporteurs {
        display: flex;
        align-items: center;

      }

      img {
        padding: 0 13px 20px;
        height: 20px;
      }

      .logo img {
        padding: 0;
        height: 60px;
      }

      @media (max-width: 740px) {
        width: 50%;
        margin-bottom: 30px;
        text-align: center;

      }
    }
  }

  .copyright-bar {
    background-color: $color-dark-blue;
    color: #fff;
    text-align: center;

    p {
      padding: 10px;
      margin: 0;
    }
  }
}