.cgv-container {
  margin: 10px;
  text-align: justify;

  h1, h2 {
    font-weight: bold;
    text-align: center;
    margin-top: 40px;
  }

  .cgv-date {
    text-align: right;
    margin-bottom: 20px;
  }

  .cgv-content {
    margin-top: 20px;

    .liste-point {
      margin-left: 50px;
    }

    .liste-tiret {
      margin-left: 80px;
      list-style-type: none;
      margin-top: 0;

      li:before {
        content: "-";
        margin-right: 10px;
      }
    }

    .liste-rien {
      margin-left: 80px;
      list-style-type: none;
    }
  }
}

