.page {
  position: relative;

  .panier-container {
    display: flex;
    justify-content: center;
    margin: 30px 0 100px;

    .panier-produits {
      margin: 10px 30px;

      .table-produits {
        border-collapse: collapse; /* Pour fusionner les bordures */

        .titres-table {
          text-align: center; /* Pour centrer le texte des titres */
          background-color: $color-dark-blue;
          color: #FFFFFF;
          font-size: 1.1rem;
          font-weight: bold;

          td {
            padding: 10px;
            white-space: nowrap;
          }

          .marge {
            padding: 10px 15px;
          }
        }

        .table-produit {
          border-bottom: 1px solid rgba(128, 128, 128, 0.5);

          .produit-td {
            padding: 10px;
            max-width: 600px;

            .produit {
              display: flex;
              align-items: center;

              img {
                max-width: 150px;
                max-height: 150px;
              }
            }
          }

          .prix-td {
            text-align: center;
            padding: 10px 15px;
          }

          .quantite-td {
            text-align: center;
            padding: 10px 20px;

            .quantite-td-div {
              display: flex;

              button {
                width: 30px;
                height: 30px;

                span {
                  font-size: 1.5rem;
                  color: $color-dark-blue;
                }
              }

              input {
                width: 30px;
                text-align: center;
                border: 1px solid $color-dark-blue;
                border-radius: 5px;

              }
            }

          }

          .total-td {
            text-align: center;
            padding: 10px 15px;
            //font-size: 1.1rem;
            font-weight: bold;
          }

          button {
            width: 50px;
            height: 50px;
            border: none;
            cursor: pointer;
            background: whitesmoke;

            span {
              font-size: 2.1rem;

              &:hover {
                font-weight: bold;
                transition: 0.3s ease;
              }
            }

          }
        }
      }

      .bouttons-panier {
        display: flex;
        align-items: center;
        justify-content: space-around;
        padding: 50px;

        button {
          padding: 20px 10px;
          width: 240px;
          font-size: 1.2rem;
          border: 1px solid $color-dark-blue;
          border-radius: 40px;
          box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
          background: $color-dark-blue;
          color: white;
          cursor: pointer;

          &:hover {
            opacity: 0.8;
            transition: 0.3s ease;
          }
        }
      }
    }

    .panier-recapitulatif {
      border: 1px solid $color-dark-blue;
      border-radius: 10px;
      height: 340px;
      width: 30%;
      max-width: 270px;
      padding: 30px;
      margin: 10px;
      font-size: 1.2rem;

      h2 {
        margin: 5px 0 30px;
        color: $color-dark-blue;
      }

      .prix {
        .details-prix {
          padding-bottom: 20px;

          .detail {
            padding: 3px 0;
            display: flex;
            justify-content: space-between;
          }
        }

        .remise {
          display: flex;
          justify-content: space-between;
          padding-bottom: 25px;
          border-bottom: 1px solid $color-dark-blue;
        }

        .prix-final {
          display: flex;
          justify-content: space-between;
          padding: 25px 0 40px;
          font-weight: bold;
        }
      }

      .button-valider {
        display: flex;
        justify-content: center;

        button {
          padding: 15px 15px;
          width: 250px;
          font-size: 1.2rem;
          border: 1px solid $color-dark-blue;
          border-radius: 30px;
          box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
          background: $color-dark-blue;
          color: white;
          cursor: pointer;

          &:hover {
            opacity: 0.8;
            transition: 0.3s ease;
          }
        }
      }

      .modal-overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.5); /* Fond semi-transparent pour l'arrière-plan */
        z-index: 1000; /* Assure que le modal est au-dessus de tous les autres éléments */
        display: flex;
        justify-content: center;
        align-items: center;

        .modal {
          background-color: white;
          padding: 20px;
          border-radius: 5px;

          .modal-content {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: center;

            .close {
              position: absolute;
              top: 0;
              right: 0;
              cursor: pointer;
            }

            p {
              margin: 30px 10px 0;
            }

            button {
              margin: 30px 0 20px;
              padding: 10px 5px;
              width: 250px;
              font-size: 1.2rem;
              border: 1px solid $color-dark-blue;
              border-radius: 30px;
              box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
              background: $color-dark-blue;
              color: white;
              cursor: pointer;

              &:hover {
                opacity: 0.8;
                transition: 0.3s ease;
              }
            }
          }
        }
      }
    }
  }

  .produit-vide {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px 50px 50px;

    h2 {
      font-size: 1.6rem;
      color: $color-dark-blue;
    }

    button {
      padding: 15px 5px;
      width: 300px;
      font-size: 1.3rem;
      border: 1px solid $color-dark-blue;
      border-radius: 30px;
      box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
      background: $color-dark-blue;
      color: white;
      cursor: pointer;
      margin-top: 20px;

      &:hover {
        opacity: 0.8;
        transition: 0.3s ease;
      }
    }
  }

  .pas-connecte {
    margin: 20px 50px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    h2 {
      font-size: 1.6rem;
      color: $color-dark-blue;
    }

    p {
      font-size: 1.2rem;
      margin-bottom: 50px;
    }

    button {
      padding: 10px 5px;
      width: 220px;
      font-size: 1.3rem;
      border: 1px solid $color-dark-blue;
      border-radius: 30px;
      box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
      background: $color-dark-blue;
      color: white;
      cursor: pointer;

      &:hover {
        opacity: 0.8;
        transition: 0.3s ease;
      }
    }
  }
}