.body {
  padding-bottom: 50px;

  .detail-produit {
    display: flex;
    flex: 1;
    margin: 70px 15px;
    justify-content: center;
    flex-direction: row;
    align-items: center;

    .image-produit {
      margin-right: 50px;
      min-width: 400px;
      max-width: 500px;
      max-height: 500px;
    }

    .couleur-titre {
      color: $color-dark-blue;
      font-family: Poppins, serif;
      font-size: 1.9rem;
      font-style: normal;
      font-weight: bold;
      line-height: normal;
      margin: 0;
    }

    .font-prix {
      color: #4A4A4A;
      font-family: Poppins, serif;
      font-size: 1.7rem;
      font-style: normal;
      font-weight: 600;
      line-height: normal;
      margin: 20px 0 10px;
    }

    .groupe-stock {
      display: flex;
      justify-content: left;
      align-items: center;
      padding-bottom: 20px;
      gap: 12px;
      border-bottom: 1px solid rgba(128, 128, 128, 0.5);

      .texte-dispo {
        color: $color-black;
        font-family: Poppins, serif;
        font-size: 1.2rem;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin: 10px 0 0;
      }

      .texte-vert {
        color: #30BD57;
        font-family: Poppins, serif;
        font-size: 1.2rem;
        font-style: normal;
        font-weight: 500;
        line-height: normal;
        margin: 10px 0 0;
      }
    }


    .bouttons {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 15px;
      margin: 30px 0;

      .quantite-td-div {
        display: flex;

        button {
          border: none;
          background: whitesmoke;
          width: 50px;
          height: 40px;
          cursor: pointer;

          &:hover {
            font-weight: bold;
          }

          span {
            font-size: 2rem;
            color: $color-dark-blue;
          }
        }

        input {
          width: 40px;
          text-align: center;
          border: 1px solid $color-dark-blue;
          border-radius: 5px;
          font-size: 1.3rem;

        }
      }

      .ajout-panier {
        display: flex;
        width: 240px;
        height: 60px;
        justify-content: center;
        align-items: center;
        cursor: pointer; // Change le curseur en pointer
        transition: opacity 0.3s; // Ajoute une transition fluide

        gap: 16.212px;
        border-radius: 30px;
        border: 1px solid $color-dark-blue;
        background: $color-dark-blue;
        box-shadow: 0 1.5px 3px rgba(0, 0, 0, 0.5);
        color: white;
        font-size: 1.3rem;

        &:hover {
          opacity: 0.7;
        }
      }
    }
  }

  .contour-description {
    margin: 20px 50px 80px;
    border: 1px solid rgba(128, 128, 128, 0.5);
    border-radius: 20px;
    padding: 20px;

    .titre-description {
      color: $color-dark-blue;
      font-family: Poppins, serif;
      font-size: 1.5rem;
      margin: 5px 0 20px 0;
    }

    .texte-description {
      font-family: Poppins, serif;
      font-size: 1.2rem;
      text-align: justify;
      margin: 0;
    }
  }

  .composition-titre {
    font-size: 1.6rem;
    color: $color-dark-blue;
    margin: 20px 10px 25px;
  }

  .table-container {
    display: flex;
    justify-content: center;
    margin: 20px 20px 50px;

    .tableau-composition {
      border-collapse: collapse; /* Pour fusionner les bordures */
      width: 100%;
      max-width: 700px;
      font-size: 1.1rem;

      tr {
        background: #D8E5E7;

        td {
          padding: 9px 12px;
        }
      }

      .titres-tableau {
        background-color: #003F62;
        color: #FFFFFF;
      }

      .fond-gris {
        background: whitesmoke;
      }

      .valeur {
        text-align: center;
        font-weight: bold;
        width: 160px;
      }


    }
  }

  .produit {
    margin: 0 30px;
    border-bottom: 1px solid rgba(128, 128, 128, 0.5);

    .titre-produit {
      display: flex;
      justify-content: space-between;

      h2 {
        margin: 11px 7px;
        font-size: 1.42rem;
      }

      button {
        background: whitesmoke;
        border: 0;
        color: grey;
        font-size: 0.75rem;
        cursor: pointer;
        padding: 0 10px;

        &:hover {
          color: lightgrey;
          transition: color 0.3s ease;
        }

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}