h1 {
  text-align: center;

  .texte-bleu {
    color: $color-blue;
  }
}

.bloc-boutiques {
  padding: 20px 30px 100px;

  .boutiques {
    border-bottom: 1px solid rgba(128, 128, 128, 0.5);

    .titre-boutique {
      display: flex;
      justify-content: space-between;

      h2 {
        margin: 11px 7px;
      }

      button {
        background: whitesmoke;
        border: 0;
        color: grey;
        font-size: 0.75rem;
        cursor: pointer;
        padding: 0 10px;

        &:hover {
          color: lightgrey;
          transition: color 0.3s ease;
        }

        svg {
          width: 20px;
          height: 20px;
        }
      }
    }

    .contenu-boutique {
      display: none;
    }

    .contenu-boutique-visible {
      display: block;
      padding: 0 0 50px;

      .detail-boutique {
        display: flex;
        justify-content: space-around;
        align-items: center;
      }

      p {
        padding: 0 0 0 50px;
      }

      img {
        margin: 0 auto;
        display: block;
        max-width: 350px;
        max-height: 350px;
        padding-top: 10px;
      }

      .favori {
        text-align: center;

        button {
          margin: auto;
          padding: 8px 20px;
          background: $color-dark-blue;
          border: 1px solid $color-dark-blue;
          border-radius: 10px;
          color: whitesmoke;
          font-size: 1rem;
          cursor: pointer;

          &:hover {
            color: $color-light-blue;
            font-size: 1.01rem;
            transition: color 0.3s ease;
          }
        }
      }
    }
  }
}