.inscription-page {
  margin: 0;
  padding: 50px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(195, 229, 243, 0.8);
  min-height: 100vh;

  form {
    width: 410px;
    background: whitesmoke;
    padding: 30px;
    border: 1px solid $color-dark-blue;
    border-radius: 10px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
    position: relative;

    h2 {
      color: $color-dark-blue;
      margin-top: 5px;
      margin-bottom: 30px;
      position: relative;

      &:before {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        height: 3px;
        width: 30px;
        border-radius: 12px;
        background: #003F62;
      }
    }

    div {
      display: flex;
      flex-direction: column;
      margin: 20px 0;

      label {
        letter-spacing: 0.5px;
      }

      input {
        margin: 7px 0 7px;
        border: 1px solid rgba(128, 128, 128, 0.5);
        background: whitesmoke;
        height: 40px;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
        border-radius: 5px;
        font-size: 1.1rem;
        padding-left: 10px;
        transition: 0.2s;

        &:focus {
          border: 1px solid $color-dark-blue;
        }
      }

      select {
        margin: 7px 0 7px;
        border: 1px solid rgba(128, 128, 128, 0.5);
        background: whitesmoke;
        height: 45px;
        box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
        border-radius: 5px;
        font-size: 1.1rem;
        padding-left: 10px;
        transition: 0.2s;
        cursor: pointer;

        option {
          color: $color-black;
        }
      }

      .error-message {
        color: red;
        font-weight: bold;
        font-size: 0.97rem;
      }

      .error-message-placeholder {
        height: 18px;
      }

      p {
        height: 6px;
        margin: -2px 0 2px;
        border-radius: 50px;
        transition: 0.3s;
        width: 33%;
      }

      .progressRed {
        background: red;
        width: 33%;
        visibility: visible;
      }

      .progressBlue {
        background: #16d9e7;
        width: 67%;
        visibility: visible;
      }

      .progressGreen {
        width: 100%;
        background: #5ef9a4;
        visibility: visible;
      }

      .bouton-password {
        position: absolute;
        right: 35px;
        top: 584px;
        height: 35px;
        width: 35px;
        font-size: 1.2rem;
        cursor: pointer;
        border: none;
        background: whitesmoke;
        color: gray;

        &:hover {
          color: black;
        }
      }

      .bouton-password-verif {
        position: absolute;
        right: 35px;
        top: 703px;
        height: 35px;
        width: 35px;
        font-size: 1.2rem;
        cursor: pointer;
        border: none;
        background: whitesmoke;
        color: gray;

        &:hover {
          color: black;
        }
      }

      .bouton-password-verif-error {
        position: absolute;
        right: 35px;
        top: 722px;
        height: 35px;
        width: 35px;
        font-size: 1.2rem;
        cursor: pointer;
        border: none;
        background: whitesmoke;
        color: gray;

        &:hover {
          color: black;
        }
      }
    }

    .cgu {
      display: flex;
      flex-direction: row;
      justify-content: center;
      align-items: center;
      margin: 0 0 25px;
      font-size: 1.1rem;

      input {
        box-shadow: none;
        margin: 0 10px 0 0;
        width: 20px;
        cursor: pointer;
      }

      a {
        color: $color-blue;
        cursor: pointer;

        &:hover {
          color: $color-light-blue;
          transition: color 0.2s ease;
        }
      }
    }

    .submit-container {
      text-align: center;

      button {
        margin: auto;
        padding: 8px 20px;
        height: 45px;
        width: 100%;
        background: $color-dark-blue;
        border: 1px solid $color-dark-blue;
        border-radius: 5px;
        color: whitesmoke;
        font-size: 1.1rem;
        cursor: pointer;

        &:hover {
          color: $color-light-blue;
          font-size: 1.11rem;
          transition: color 0.3s ease;
        }
      }

      p {
        font-weight: bold;
        width: 100%;
        margin: 20px 0;
      }

      h3 {
        font-size: 0.9rem;
        margin-bottom: 5px;

        a {
          color: $color-blue;
          cursor: pointer;

          &:hover {
            color: $color-light-blue;
            transition: color 0.2s ease;
          }
        }
      }
    }

  }
}